import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import CircularProgress from '@mui/material/CircularProgress';
// core import
import { useSelector, useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import React, { useEffect, useState } from 'react';
import { blue } from '@mui/material/colors';

// Dialogs

import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogContentText from '@mui/material/DialogContentText';
import DialogTitle from '@mui/material/DialogTitle';
import {
  selectCategoryErrors,
  selectCategoryError,
  selectCategoryUpdateStatus,
  selectSelectedCategory,
  setCategoryError,
  setCategoryErrors,
  setCategoryUpdateStatus,
  updateSelectedCategory,
} from 'features/main/mainSlice';
import { selectAccessToken } from 'features/user/userSlice';
import { updateCategoryAsyncThunk } from 'features/category/categorySlice';

export default function CategoryEditView() {
  // core
  const navigate = useNavigate();
  const dispatch = useDispatch();

  // internal state
  const [formData, setFormData] = useState({
    placement: 0,
    shop_id: 0,
    name: '',
    description: '',
  });

  // data
  const selectedCategory = useSelector(selectSelectedCategory);
  const accessToken = useSelector(selectAccessToken);
  const categoryUpdateStatus = useSelector(selectCategoryUpdateStatus);
  const categoryError = useSelector(selectCategoryError);
  const categoryErrors = useSelector(selectCategoryErrors);

  const handleChange = (e) => {
    const { name, value } = e.target;
    console.log({ name, value });
    dispatch(updateSelectedCategory({ name, value }));
    setFormData({ ...formData, [name]: value });
  };

  const handleSubmit = (event) => {
    console.log(event);
    event.preventDefault();

    // validate form data
    // Validation logic
    const validationErrors = {};

    /*
    if (!formData.name) {
      validationErrors.name = 'Name is required';
    }

    if (!formData.description) {
      validationErrors.description = 'Description is required';
    }
    */
    console.log(validationErrors);

    if (Object.keys(validationErrors).length > 0) {
      // alert("validation error");
      // setErrors(validationErrors);
      dispatch(setCategoryErrors(validationErrors));
    } else {
      dispatch(setCategoryErrors({}));
      const data = new FormData(event.target);
      dispatch(updateCategoryAsyncThunk(data));
    }
  };

  const handleClose = () => {
    dispatch(setCategoryUpdateStatus('idle'));
  };

  useEffect(() => {
    if (categoryUpdateStatus == 'succeeded') {
      dispatch(setCategoryUpdateStatus('idle'));
      // dispatch(updateCategory(selectedCategory));
      navigate(-1); // This is equivalent to pressing the back button
    }
  }, [categoryUpdateStatus]);

  return (
    <>
      <Container component="main" maxWidth="xs">
        <Box
          sx={{
            marginTop: 8,
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
          }}
        >
          <Typography component="h1" variant="h5">
            Edit Category
          </Typography>
          <Box component="form" onSubmit={handleSubmit} noValidate sx={{ mt: 1 }}>
            <input
              name="access_token"
              value={accessToken}
              type="hidden"
              placeholder="Hidden Input"
            />
            <input
              name="id"
              value={selectedCategory.id}
              type="hidden"
              placeholder="Hidden Input for Selected Category Id"
            />
            <input
              name="shop_id"
              value={selectedCategory.shop_id}
              type="hidden"
              placeholder="Hidden Input for Selected Shop Id"
            />
            <input
              name="placement"
              value={selectedCategory.placement}
              type="hidden"
              placeholder="Hidden Input"
            />
            {/* name */}
            <TextField
              id="name"
              label="Name"
              name="name"
              value={selectedCategory.name ?? ""}
              onChange={handleChange}
              error={categoryErrors.hasOwnProperty('name')}
              helperText={categoryErrors.name}
              autoComplete="name"
              margin="normal"
              required
              fullWidth
              autoFocus
            />

            {/* description */}
            <TextField
              id="description"
              label="Description"
              name="description"
              value={selectedCategory.description ?? ""}
              autoComplete="description"
              onChange={handleChange}
              error={categoryErrors.hasOwnProperty('description')}
              helperText={categoryErrors.description}
              margin="normal"
              required
              fullWidth
            />

            <Box sx={{ m: 1, position: 'relative' }}>
              <Button
                type="submit"
                fullWidth
                variant="contained"
                sx={{ mt: 3, mb: 2 }}
                disabled={categoryUpdateStatus === 'loading'}
              >
                Update Cartegory
              </Button>
              {categoryUpdateStatus == 'loading' && (
                <CircularProgress
                  size={24}
                  sx={{
                    color: blue[500],
                    position: 'absolute',
                    top: '50%',
                    left: '50%',
                    marginTop: '-12px',
                    marginLeft: '-12px',
                  }}
                />
              )}
            </Box>
          </Box>
        </Box>

        <Dialog
          open={categoryUpdateStatus === 'error'}
          onClose={handleClose}
          aria-labelledby="alert-dialog-title"
          aria-describedby="alert-dialog-description"
        >
          <DialogTitle id="alert-dialog-title">{'Error'}</DialogTitle>
          <DialogContent>
            <DialogContentText id="alert-dialog-description">{categoryError}</DialogContentText>
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose} autoFocus>
              OK
            </Button>
          </DialogActions>
        </Dialog>
      </Container>
    </>
  );
}
